import './App.css';
import React, {useState} from 'react';

const SentencePart = () => {

  const randomize = (arr) => {
    const randomizedNumbers = new Array(6)

    for (let i=0; i<=5; i++){
      let one = Math.floor(Math.random() * arr[i]) + 1
      randomizedNumbers[i] = one
    }
    return randomizedNumbers
  }

  const arrayCreate = (jsonDatas) => {
    const numbersOfKeys = new Array(6)
    var i = 0

    for (const property in jsonDatas) {
      numbersOfKeys[i] = Object.keys(jsonDatas[property]).length;
      i++
    }
    let rt = randomize(numbersOfKeys)
    return rt
  }

  const oneWordEach = (params, randomNumbers) => {
    let i = 0
    let word = new Array(6)
    for (const property in params) {
      word[i] = params[property][randomNumbers[i]]
      i++
    }
   return word
}

  const fth = () => {
    let rtrn = new Array(6)
    fetch("teksty.json")
    .then(response => response.json())
    .then(datas => {
      rtrn = arrayCreate(datas)  // rtrn to wylosowane liczby
      const effect = oneWordEach(datas, rtrn)
      setData(words = effect)
    })
  }

  let [words, setData] = useState(["Witaj.","Kliknij/dotknij przycisk,", "aby wylosować sobie zdanie nt teoriospiskowe.", "Ale nie łudź. Nie będzie ono bardziej sensowne", "od (większości) wymysłów,", "które możesz znaleźć w internecie"]);

  return (
    <div className="containerOfAll">
      
    <div className="containerOfSentence">
        <div className="partOfSentence">
        <p>{words[0]}</p>
        </div>
        <div className="partOfSentence">
        <p>{words[1]}</p>
        </div>
        <div className="partOfSentence">
        <p>{words[2]}</p>
        </div>
        <div className="partOfSentence">
        <p>{words[3]}</p>
        </div>
        <div className="partOfSentence">
        <p>{words[4]}</p>
        </div>
        <div className="partOfSentence">
        <p>{words[5]}</p>
        </div>
    </div>
    <button className="btn" onClick={fth}>utwórz zdanie</button>

    </div>
  );
};

export default SentencePart;